import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./contract-priority-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class ContractPriorityAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
//	@ViewChild(ToastComponent) toast: ToastComponent;

 
contractPriorityForm: FormGroup;
contractpriorityId = 0;  
ajaxData : ContractPriorityData[];
contractpriority= new ContractPriorityData();
status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.contractpriorityId).subscribe(params=>{
			if(params.contractpriorityId != undefined){
				this.contractpriorityId=params.contractpriorityId; 
			}
		}); 
	console.log("contractpriorityId=="+this.contractpriorityId);
	if(this.contractpriorityId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.contractPriorityForm = new FormGroup({			
			'contractpriorityName' : new FormControl('', [ Validators.required]),
			'contractpriorityDesc' : new FormControl('', [ Validators.required]),
			'contractpriorityDuehrs' : new FormControl('', [ Validators.required]),
			'contractpriorityTrigger1Hrs' : new FormControl(''),
			'contractpriorityTrigger2Hrs' : new FormControl(''),
			'contractpriorityTrigger3Hrs' : new FormControl(''),
			'contractpriorityTrigger4Hrs' : new FormControl(''),
			'contractpriorityTrigger5Hrs' : new FormControl(''),
			'contractpriorityBusinessHrs' : new FormControl(''),
		});
	}
	  
	get contractpriorityName(){ return this.contractPriorityForm.get('contractpriorityName')};
	get contractpriorityDesc(){ return this.contractPriorityForm.get('contractpriorityDesc')};
	get contractpriorityRank(){ return this.contractPriorityForm.get('contractpriorityRank')};
	get contractpriorityDuehrs(){ return this.contractPriorityForm.get('contractpriorityDuehrs')};
	get contractpriorityTrigger1Hrs(){ return this.contractPriorityForm.get('contractpriorityTrigger1Hrs')};
	get contractpriorityTrigger2Hrs(){ return this.contractPriorityForm.get('contractpriorityTrigger2Hrs')};
	get contractpriorityTrigger3Hrs(){ return this.contractPriorityForm.get('contractpriorityTrigger3Hrs')};
	get contractpriorityTrigger4Hrs(){ return this.contractPriorityForm.get('contractpriorityTrigger4Hrs')};
	get contractpriorityTrigger5Hrs(){ return this.contractPriorityForm.get('contractpriorityTrigger5Hrs')};
	get contractpriorityBusinessHrs(){ return this.contractPriorityForm.get('contractpriorityBusinessHrs')};
	 
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<ContractPriorityData>(sessionStorage.getItem('requestUrl')+'/axelacrm/service/contractpriority-data ',
							 			JSON.stringify({ 'contractpriorityId' :this.contractpriorityId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.contractpriority = data['populatadata'][0];
																		console.log("contractpriorityModifiedId=="+this.contractpriority.contractpriorityModifiedId);
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['portal/customer-group-list']);      //path should be changed
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.contractpriority[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.contractPriorityForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.contractpriorityId == 0){ 
				this.contractpriority['requestType'] = 'add';
			}else{
				this.contractpriority['requestType'] = 'update';
				this.contractpriority['contractpriorityId'] = this.contractpriorityId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/contractpriority',
									JSON.stringify(this.contractpriority),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
	//			  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.contractPriorityForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.contractpriority);
	  }
 
  onDelete(){
		 this.contractpriority['requestType'] = 'delete';
		this.contractpriority['contractpriorityId'] = this.contractpriorityId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/service/contractpriority',
									JSON.stringify(this.contractpriority),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
//			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.contractPriorityForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

	
export class  ContractPriorityData {
    contractpriorityId: string;
    contractpriorityName: string;
	contractpriorityDesc: string;
	contractpriorityRank: string;
	contractpriorityDuehrs: string;
	contractpriorityTrigger1Hrs: string;
	contractpriorityeTrigger2Hrs: string;
	contractpriorityTrigger3Hrs: string;
	contractpriorityTrigger4Hrs: string;
	contractpriorityTrigger5Hrs: string;
	contractpriorityBusinessHrs: string;
	contractpriorityEntryId: string;
	contractpriorityEntryDate: string;
	contractpriorityModifiedId: string;
	contractpriorityModifiedDate: string;
  
}


